import './Option.css';


function Option ({ option, labelClassName, onChange, toggleValue }) {



  function handleChange(e) {
    onChange ? onChange(e.target.value) : console.log('change')
  }

  return (
    <label className='filter-radio__label'>
      <input
        type='radio'
        name='radio'
        value={option.value}
        className='filter-radio__input filter-radio__input_invisible'
        onChange={handleChange}
        // checked={toggleValue === option.value}
      />
      <span className='filter-radio__input_visible'/>
      <p className={'filter-radio__labe-subtitle ' + labelClassName}>{option.label}</p>
    </label>
  );
}

export default Option;