import './ReviewsCasting.css';
import Review from './Review/Review';

function ReviewsCasting ( {reviews} ) {
  return (
    <div className='reviews-casting'>
      <p className='reviews-casting__subtitle'></p>
      <h2 className='reviews-title'>        </h2>
      <p className='reviews-casting__subtitle'></p>
      <div className='revies-casting__review'>
      {reviews.slice(0, reviews.length).map((review) =>
        (<Review photo={review.photo} name={review.name} text={review.text} raiting={review.raiting}/>)
        )}
      </div>
    </div>

  )
}

export default ReviewsCasting;