import './Header.css';
import { useState } from 'react';
import { NavLink } from 'react-router-dom';
import HeaderLogo from '../HeaderLogo/HeaderLogo';
import useScreenWidth from "../../utils/useScreenWidth";
import profilePhoto from '../../images/Photo/header-photo.png';
import MenuProfile from './MenuProfile/MenuProfile';

function Header({ loggedIn }) {
  const size =  useScreenWidth();

  const [isMenuProfileOpen, setIsMenuProfileOpen] = useState(false);
  const menuProfileClassName = (
    `${isMenuProfileOpen ? 'menu-profile' : 'menu-profile_invisible'}`
  );

  function handleProfileClick() {
    setIsMenuProfileOpen(!isMenuProfileOpen)
  }

  return (
    <header className='header'>
      {size.width>768 ?
        ''
        : <button className='heder__mobile-menu'/>}
      <div className='header__container'>
        <HeaderLogo></HeaderLogo>
        {size.width>768 ? 
          <nav className='header__links'>
            <NavLink exact to="/" className='header__link' activeClassName='header__link_active'></NavLink>
            <NavLink exact to="/castings" className='header__link' activeClassName='header__link_active'></NavLink>
            <NavLink exact to="/about" className='header__link' activeClassName='header__link_active'> </NavLink>
          </nav>
          : ''
        }
      </div>
      {loggedIn ?
        <>
          <div className='header__actor-profile' onClick={handleProfileClick}>
            <h2 className='header__actor-profile-name'> </h2>
            <p className='header__actor-profile-email'>Sasha88@gmail.com</p>
            <img className='header__actor-profile-photo' src={profilePhoto} alt=' ' />
            <span className='header__notice'></span>
            <button className='header__bell'/>
            <MenuProfile menuProfileClassName={menuProfileClassName} handleProfileClick={handleProfileClick} isMenuProfileOpen={isMenuProfileOpen} size={size}/>
          </div>
        </>
        :
        <NavLink exact to="/signin" className='header__link header__link_type_signin'>
          <p className='header__link-text'>
            
          </p>
          <div className='header__link-img'></div>
        </NavLink>
      
      }
    </header>
  )
}

export default Header;