import './Tabs.css';
import { useState } from 'react';
import WithdrawFunds from './WithdrawFunds/WithdrawFunds';
import AddMoney from './AddMoney/AddMoney';
import History from './History/History';

function Tabs() {
  const [ activeTab, setActiveTab ] = useState('withdraw');

  function handleTab(e) {
    setActiveTab(e.target.getAttribute('data-name'));
  }

  const FinanceTab = () => {
    if(activeTab === 'withdraw') {
      return <WithdrawFunds />
    } else if (activeTab === 'add-money') {
      return <AddMoney />
    } else {
      return <History />
    }
  }

  return (
    <section className="tabs">
      <div className="tabs__buttons">
        <button data-name='withdraw' onClick={handleTab} className={`tabs__button ${activeTab === 'withdraw' ? 'tabs__button_active' : ''}`}></button>
        <button data-name='add-money' onClick={handleTab} className={`tabs__button ${activeTab === 'add-money' ? 'tabs__button_active' : ''}`}></button>
        <button data-name='history' onClick={handleTab} className={`tabs__button ${activeTab === 'history' ? 'tabs__button_active' : ''}`}></button>
      </div>
      <FinanceTab />
    </section>
  )
}

export default Tabs;