import './RadioFilter.css';
import Option from './Option/Option';

function RadioFilter({ options, title, titleClassName, labelClassName, containerClassName, onChange, toggleValue }) {
  
  return (
    <div className='filter-radio'>
      <h3 className={'filter-radio__title ' + titleClassName}>{title}</h3>
      <div className={'filter-radio__container ' + containerClassName}>
        {options.slice(0, options.length).map((option) =>
        (<Option option={option} key={option.id} labelClassName={labelClassName} onChange={onChange} toggleValue={toggleValue}/>)
        )}
    </div>
  </div>
  );
}

export default RadioFilter;